body, html {
  margin: 0;
  padding: 0;
}
.BeersApp {
  /*text-align: center;*/
  display: grid;
  grid-template-columns: 30% 70%;
  height: 100vh;
}

.Beer-List {
  position: relative;
  background: #e6e6e6;
  padding: 0 1rem;
}

.Beer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.Beer small {
  display: block;
}

.Beer-Image {
  width: 30px;
}

.Beer-Image img {
  max-width: 100%;
}

.Search {
  padding: 3rem 1rem;
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

button {
  cursor: pointer;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  display: -ms-flexbox;
  display: flex;
  padding-bottom: .5em;
  margin-bottom: .5em;
  border-bottom: 1px solid #eaeaea;
}

li button {
  margin-left: auto;
}

.user {
  display: -ms-flexbox;
  display: flex;
}

.user figure {
  width: 50px;
  margin: 0;
  margin-right: 1rem;
}

.user img {
  width: 50px;
}



