.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


button {
  cursor: pointer;
}

body {
  padding: 1rem;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  display: -ms-flexbox;
  display: flex;
  padding-bottom: .5em;
  margin-bottom: .5em;
  border-bottom: 1px solid #eaeaea;
}

li button {
  margin-left: auto;
}

.user {
  display: -ms-flexbox;
  display: flex;
}

.user figure {
  width: 50px;
  margin: 0;
  margin-right: 1rem;
}

.user img {
  width: 50px;
}



